@import url(https://fonts.googleapis.com/css2?family=Quicksand);
.Header{display:flex;align-items:center;justify-content:center;height:6vh;color:gray;border-radius:20px;box-shadow:2px 2px 3px 2px #DDDDDD}.Header .content{margin-left:20px;font-size:16pt}

.Keg{align-self:flex-start;border:1px solid lightgray;border-radius:10px;margin:8px;padding:8px;box-shadow:1px 1px 1px 0px #EFEFEF}.Keg:hover{box-shadow:1px 1px 1px 0px #DADADA}summary{outline:none}

.Info{min-width:240px}.Info hr{border:none;border-top:1px solid lightgray}.Info .within-details{font-size:9pt;margin-left:20px}

.KegList{font-size:10pt}.KegList button{padding:3px 10px 3px 10px;box-shadow:1px 1px 2px 1px #CCCCCC;border-radius:5px;border:none;background:white;margin-right:5px}.KegList label{color:orange}.KegList summary{cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.KegList .keg-name{font-size:12pt}.KegList .keg-price{color:darkgreen}.KegList .flexbox{display:flex;flex-wrap:wrap}.KegList .warning{color:red;margin-left:10px}.KegList .add-keg-outer{cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.KegList .add-keg-inner{color:gray;font-size:30pt;font-weight:100}.KegList .add-keg-inner:hover{color:black}

.App{font-family:'Quicksand', sans-serif;font-weight:300}.App .appgrid{display:grid;grid-template-columns:1fr;grid-template-rows:1fr 8fr;grid-gap:20px;height:calc(100vh - 46px);width:calc(100vw - 46px);padding:15px}.App .header{grid-area:1 / 1 / 1 / 2}.App .keg-list{grid-area:2 / 1 / 2 / 2}.App button:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}

